@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+Mono&display=swap);
* {
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

body {
  font-family: 'Noto Sans Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f8f4f1;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.TabBar {

	background-color: #e00600;
	margin-bottom: 20px;

}


.TabButton {
	display: table-cell;
	color: white;
	font-size: x-large;
	padding-block : 15px;
	padding-inline: 15px;
	-webkit-margin-end: 30px;
	        margin-inline-end: 30px;
	transition: 0.3s;
}

.TabButton:hover {
	background-color: black;
}


.LogoImage {
	float: left;
	position: -webkit-sticky;
	position: sticky;
	height: 45px;
	padding-top: 10px;
	-webkit-margin-end: 100px;
	        margin-inline-end: 100px;
	
}

.LeftList {
	text-align: center;
	overflow: hidden;
	overflow-y: scroll;
	overflow-x: scroll;
	padding-block: 10px;
	padding-inline: 20px;
	width: 250px;
	list-style-type: none;
	
}

.ListItem {
	font-weight: 500;
	text-decoration: none;
	display: block;
	color: black;
	cursor: pointer;
}

.ListItem:link{
	text-decoration: none;
	color: black;
}

.ListItem:not(:last-child) {
	margin-bottom: 15px;
}



.ListHeading {
	display: inline-block;
	margin-bottom: 10px;
	-webkit-padding-after: 5px;
	        padding-block-end: 5px;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	-webkit-border-after: 2px dashed;
	        border-block-end: 2px dashed;
	border-block-end-color: #e00600;
	margin-top: 10px;
	font-size: larger;
	font-weight: 600;
	padding-inline: 10px;
}


.MyScroll::-webkit-scrollbar{
	width: 6px;
	height: 4px;
}

.MyScroll::-webkit-scrollbar-track{
	background: white;
	border-radius: 20px;
}

.MyScroll::-webkit-scrollbar-thumb{
	background-color: rgba(0, 0, 0, 0.3);
	border-radius: 25px;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  background-color: white;
}

.card:hover {
	box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}


.ListBlock{	
	-webkit-padding-start: 10px;	
	        padding-inline-start: 10px;
	background-color: white;
	-webkit-padding-after: 5px;
	        padding-block-end: 5px;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	
	
}


.LeftPane {
	display: grid;
	grid-row-gap: 20px;
	margin-left: 20px;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
}

.YearList {
	font-size: 1.4rem;
	height: 150px;
}

.RaceList {
	font-size: 1.2rem;
	height: 300px;
}

.MainGrid {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	grid-column-gap: 20px;
}

.RightPane{
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	margin-right: 20px;
	grid-column: span 3;
	margin-bottom: 50px;
}


.ContentHeading {
	display: inline-block;
	margin-bottom: 10px;
	-webkit-padding-after: 5px;
	        padding-block-end: 5px;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	-webkit-border-after: 2px dashed;
	        border-block-end: 2px dashed;
	border-block-end-color: #e00600;
	font-size: larger;
	font-weight: 600;
	padding-inline: 10px;
}



.CircuitContent {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: 1fr;
	padding-block: 10px;
	margin-top: 20px;
	overflow: hidden;
	overflow-x: scroll;
	font-size: 1rem;
}


.Locate {
	color: blue;
}

.CircuitMain{
	margin-bottom: 20px;
}

h2{
	padding-top: 10px;
}
.TableMain {
	display: table;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: 1fr 1fr;
	padding-block: 10px;
	grid-row-gap: 10px;
	grid-column-gap: 10px;
	overflow: hidden;
	overflow-x: scroll;
	font-size: 1rem;
	padding-inline: 30px;
}

.TableContent{
	border-collapse: collapse;
	width: 100%;
	table-layout: fixed;
}

.TableContent th {
	padding-block: 20px;
  	text-align: center;
 	background-color: white;
	  color: grey;
	  font-weight: normal;
}

.TableContent td{
	  padding-block: 20px;
	  padding-inline: 8px;
}

.TableContent tr:nth-child(even){
	background-color: #f4f4f4;
}

